import request from '@/utils/request'
// 角色列表
export function findRoleByShopId(data) {
  return request({
    url: `/sys/sysUser/findRoleByShopId`,
    method: 'post',
    data
  })
}
// 用户列表
export function findByCondition(data) {
  return request({
    url: `/sys/sysUser/findByCondition`,
    method: 'post',
    data
  })
}
// 添加用户
export function add(data) {
  return request({
    url: `/sys/sysUser/add`,
    method: 'post',
    data
  })
}
// 编辑用户
export function update(data) {
  return request({
    url: `/sys/sysUser/update`,
    method: 'post',
    data
  })
}
// 禁用、启用
export function updateEnable(data) {
  return request({
    url: `/sys/sysUser/updateEnable`,
    method: 'post',
    data
  })
}
// 用户详情
export function findById(data) {
  return request({
    url: `/sys/sysUser/findById`,
    method: 'post',
    data
  })
}